.block {
    width: 100%;
    display: flex;
}

.block_info {
    display: grid;
    column-gap: 20px;
    row-gap: 10px;
    grid-template-rows: repeat(5, auto);
    grid-template-columns: repeat(2, auto);
}

.block_info p {
    font-size: 16px;
    font-family: Arial;
    color: black;
}
@media screen and  (max-width : 700px) {
    .block {
        flex-flow: column;
        align-items: center;
    }
    .block_info {
       margin-top: 20px;
    }
}